import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Login from './containers/Auth/Login'
import ForgotPassword from './containers/Auth/ForgotPassword'
import ResetPassword from './containers/Auth/ResetPassword'
import CompanyProfile from './containers/Onboarding/CompanyProfile'
import ImportProducts from './containers/Onboarding/ImportProducts'
import ImportCustomers from './containers/Onboarding/ImportCustomers'
import ImportTeam from './containers/Onboarding/ImportTeam'
import Regions from './containers/Onboarding/Regions'
import Products from './containers/Products'
import ProductDetail from './containers/Products/ProductDetail'
import ArchivedProducts from './containers/Products/ArchivedProducts'
import Customers from './containers/Customers'
import ArchivedCustomers from './containers/Customers/ArchivedCustomers'
import Team from './containers/Team'
import ArchivedTeam from './containers/Team/ArchivedTeam'
import Leads from './containers/Leads'
import Sales from './containers/Sales'
import CustomerDetail from './containers/Customers/CustomerDetail'
import TeamDetail from './containers/Team/TeamDetail'
import OrgChart from './containers/Team/OrgChart'
import Chat from './containers/Chat'

import AuthenticatedRoute from "./components/authenticated-route";
import UnauthenticatedRoute from "./components/unauthenticated-route";

function Routes(props) {
  console.log(props, 'routes')
  return (
    <Switch>
      <UnauthenticatedRoute path="/" exact component={Login} />
      <UnauthenticatedRoute path="/forgotpassword" exact component={ForgotPassword} />
      <UnauthenticatedRoute path="/resetpassword" exact component={ResetPassword} />

      <UnauthenticatedRoute path="/companyprofile" exact component={CompanyProfile} />
      <UnauthenticatedRoute path="/importproducts" exact component={ImportProducts} />
      <UnauthenticatedRoute path="/importcustomers" exact component={ImportCustomers} />
      <UnauthenticatedRoute path="/importteam" exact component={ImportTeam} />
      <UnauthenticatedRoute path="/regions" exact component={Regions} />

      <UnauthenticatedRoute path="/products" exact component={Products} />
      <UnauthenticatedRoute path="/productdetail" exact component={ProductDetail} />
      <UnauthenticatedRoute path="/productsarchived" exact component={ArchivedProducts} />
      <UnauthenticatedRoute path="/customers" exact component={Customers} />
      <UnauthenticatedRoute path="/archivedcustomers" exact component={ArchivedCustomers} />
      <UnauthenticatedRoute path="/team" exact component={Team} />
      <UnauthenticatedRoute path="/orgchart" exact component={OrgChart} />
      <UnauthenticatedRoute path="/archivedteam" exact component={ArchivedTeam} />
      <UnauthenticatedRoute path="/leads" exact component={Leads} />
      <UnauthenticatedRoute path="/sales" exact component={Sales} />
      <UnauthenticatedRoute path="/customerdetail" exact component={CustomerDetail} />
      <UnauthenticatedRoute path="/teamdetail" exact component={TeamDetail} />

      <UnauthenticatedRoute path="/profile" exact component={CompanyProfile} profilePage />
      <UnauthenticatedRoute path="/chat" exact component={Chat} />
    </Switch>
  );
}

export default Routes;
