import React from 'react';
import CustomIcon from '../common/CustomIcon'

function TableContent(props) {

  const renderColumns = () => {
    return <tr>{props.columns.map((item, i) => {
      return <th key={i}>{item.label}</th>
    })}
      <th></th>
    </tr>
  }

  const rendertableCell = (value, key, type) => {
    if (type === 'name') {
      return <div className="userCard">
        <div className="userCon">
          <CustomIcon icon="Placeholder/Person/Small" />
        </div>
        {value}
      </div>
    } else if (type === 'chips') {
      return <ul className="chips">
        <li>test</li>
        <li>test</li>
        <li>test</li>
      </ul>
    } else {
      return value
    }
  }

  const renderData = () => {
    return props.data.map((item, index) => {
      return <tr key={index}>
        {props.columns.map((col, i) => {
          return <td key={i}>
            {rendertableCell(item[col.key], col.key, col.type)}
          </td>
        })}
        <td align="right">
          {props.tableActions ?
            props.tableActions
            :
            <div className="table-actions">
              <button className="sendBtn">
                <CustomIcon icon="Header/Icon/More" />
              </button>
            </div>
          }
        </td>
      </tr>
    })
  }

  return (
    <div className="tableContent">
      <table cellpadding="0" cellspacing="0">
        <thead>
          {renderColumns()}
        </thead>
        <tbody>
          {renderData()}
        </tbody>
      </table>
    </div>
  );
}

export default TableContent;
